/* ==============================
  ||Header
============================== */
.header {
  height: 70px;
  background-image: url("../images/bg/bg_header.webp");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  border-bottom: 1px solid #fff;
  box-shadow: 0 2px 4px 1px rgba(0, 0, 0, 0.5);
  z-index: 10000;
}
.header__logo {
  width: 157.5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header__nav {
  display: none;
}

.drawer__btn {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 10002;
  width: 70px;
  height: 70px;
  overflow: hidden;
  cursor: pointer;
}
.drawer__btn span {
  display: block;
  width: 40%;
  height: 3px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transition: 0.3s;
}
.drawer__btn span:nth-child(1) {
  transform: translate(-50%, -12px);
}
.drawer__btn span:nth-child(2) {
  transform: translate(-50%, -50%);
}
.drawer__btn span:nth-child(3) {
  transform: translate(-50%, 10px);
}
.drawer__btn.active span:nth-child(1) {
  transform: translate(-50%, 0) rotate(45deg);
}
.drawer__btn.active span:nth-child(2) {
  transform: translate(100%, 0);
  opacity: 0;
}
.drawer__btn.active span:nth-child(3) {
  transform: translate(-50%, 0) rotate(-45deg);
}
.drawer__menu {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 10001;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  background-color: #000;
  transition: 0.3s;
  transform: translateX(100%);
  opacity: 0;
}
.drawer__menu.active {
  transform: translateX(0);
  opacity: 1;
}
.drawer__bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  transition: 0.3s;
  transform: translateX(100%);
  opacity: 0;
  cursor: pointer;
}
.drawer__bg.active {
  transform: translateX(0);
  opacity: 1;
}

.menu__list {
  color: #fff;
  padding-top: 150px;
  width: 80%;
  max-width: 450px;
  margin-left: auto;
  margin-right: auto;
}
.menu__list a {
  text-decoration: none;
  display: block;
  width: 100%;
}
.menu__item {
  line-height: 3;
  border-bottom: 1px solid #fff;
}

@media screen and (min-width: 850px) {
  .header {
    background-repeat: repeat;
    background-size: 600px;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0 0 1rem;
  }
  .header__logo {
    width: 225px;
    position: static;
    transform: translate(0, 0);
    margin-right: 1rem;
  }
  /* ==============================
    // header navigation
  ============================== */
  .header__nav {
    display: block;
  }
  .nav__list {
    display: grid;
    grid-template-columns: repeat(4, 15vw) 117.9px;
    grid-template-rows: repeat(1, 100px);
    align-items: center;
  }
  .nav__item {
    color: #fff;
    text-align: center;
    height: 60px;
    display: grid;
    align-items: center;
    letter-spacing: 4px;
    font-weight: 600;
  }
  .nav__item:hover:not(.webshop) {
    background: var(--red);
    border-radius: 30px;
  }
  .nav__item span {
    display: block;
    font-size: 0.625rem;
    letter-spacing: 1.5px;
  }
  .nav__item.webshop {
    background: var(--red);
    height: 99px;
    margin-left: 10px;
  }
  .nav__item.webshop a {
    width: 100%;
    height: 100%;
    display: grid;
    align-items: center;
    place-items: center;
    justify-items: center;
  }
  .drawer__btn {
    display: none;
  }
}
